import React from 'react';
import Header from './Header.js';
import Main from './Main.js';
import Footer from './Footer.js';
import PopupWithForm from './PopupWithForm.js';
import PopupWithImage from './PopupWithImage.js';




function App() {

  const [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] = React.useState(false);
  const [isEditProfilePopupOpen, setIsEditProfilePopupOpen] = React.useState(false);
  const [isAddPlacePopupOpen, setisAddPlacePopupOpen] = React.useState(false);
  const [selectedCard, setSelectedCard] = React.useState(false);

  function handleCardClick(card) {
    setSelectedCard(card);
  }

  function handleEditAvatarClick() {
    setIsEditAvatarPopupOpen(true);
  }

  function handleEditProfileClick() {
    setIsEditProfilePopupOpen(true);
  }

  function handleAddPlaceClick() {
    setisAddPlacePopupOpen(true);
  };

  function closeAllPopups() {
    setIsEditAvatarPopupOpen(false);
    setIsEditProfilePopupOpen(false);
    setisAddPlacePopupOpen(false);
    setSelectedCard(false)
  }

  return (
    <body className="page">

      <Header />
      <Main
        onEditProfile={handleEditProfileClick}
        onEditAvatar={handleEditAvatarClick}
        onAddPlace={handleAddPlaceClick}
        onCardClick={handleCardClick}
      />
      <Footer />

      <PopupWithForm
        name="edit-avatar"
        title="Обновить аватар"
        isOpen={isEditAvatarPopupOpen}
        onClose={closeAllPopups}
        children={
          <>
            <input type="url" id="link-input" name="link" className="form__input form__input_link"
              placeholder="Ссылка на изображение" required />
            <span className="popup__input-error" id="link-input-error"></span>
          </>
        } />

      <PopupWithForm
        name="profile"
        title="Редактировать профиль"
        isOpen={isEditProfilePopupOpen}
        onClose={closeAllPopups}
        children={
          <>
            <input type="text" id="name-input" name="name" className="form__input form__contact-name" placeholder="Имя"
              minlength="2" maxlength="40" required />
            <span className="popup__input-error" id="name-input-error"></span>
            <input type="text" id="interst-input" name="about" placeholder="О себе"
              className="form__input form__contact-interst" minlength="2" maxlength="200" required />
            <span className="popup__input-error" id="interst-input-error"></span>
          </>
        } />
      <PopupWithForm
        name="add-img"
        title="Новое место"
        isOpen={isAddPlacePopupOpen}
        onClose={closeAllPopups}
        children={
          <>
            <input type="text" id="title-input" name="name" class="form__input form__item-name"
              placeholder="Название" minlength="1" maxlength="30" required />
            <span class="popup__input-error" id="title-input-error"></span>
            <input type="url" id="link-input" name="link" class="form__input form__link-img"
              placeholder="Ссылка на картинку" required />
            <span class="popup__input-error" id="link-input-error"></span>
          </>
        } />

      <div className="block-action"></div>
      <div className="error-server"></div>


      <div className="popup popup_confirm-delete">
        <form className="popup__container popup__container_confirm-delete" name="popup__confirm-delete" novalidate>
          <button className="popup__close-button" type="button"></button>
          <h3 className="popup__title popup__title_confirm">Вы уверены?</h3>
          <button type="submit" className="form__submit form__submit_confirm-delete">Да</button>
        </form>
      </div>

      <template id="card-template">
        <article className="grid-card">
          <button className="grid-card__del" type="button"></button>
          <img src="" className="grid-card__img" alt="Изображение карточки" />
          <div className="grid-card__wrapper">
            <h3 className="grid-card__text">Камчатка</h3>
            <div className="grid-card__likes">
              <button className="grid-card__like"></button>
              <span className="grid-card__likes-count">0</span>
            </div>
          </div>
        </article>
      </template>

      <PopupWithImage
        card={selectedCard}
        onClose={closeAllPopups}
      />

    </body>
  );
}

export default App;
