import React from 'react';

function Card({card, i, onCardClick}) {
  return (
    <article className="grid-card" key={i}>
      <img
        className="grid-card__img"
        src={card.link}
        onClick={() => onCardClick(card)}
      />
      <div className="grid-card__wrapper">
        <h3 className="grid-card__text">{card.name}</h3>
        <button className="grid-card__del" type="button"></button>
        <div className="grid-card__likes">
          <button className="grid-card__like"></button>
          <span className="grid-card__likes-count">{card.likes.length}</span>
        </div>
      </div>
    </article>
  )
}

export default Card;


