import React from 'react';
import Card from './Card.js';
import { api } from '../utils/Api.js';

function Main(props) {
  const [userName, setUserName] = React.useState("Hello");
  const [userDescription, setUserDescription] = React.useState("World");
  const [userAvatar, setUserAvatar] = React.useState(false);
  const [cards, setCards] = React.useState([]);

  React.useEffect(() => {
    api.getUserInfoFromServer() // Получаем данные профиля
      .then((userData) => {
        setUserName(userData.name);
        setUserDescription(userData.about);
        setUserAvatar(userData.avatar);
      })
     
    api.getInitialCards() // Получаем массив карточек
    .then((data) => {
        setCards(data);
    })
}, []);


  return (
    <main className="content">
      <section className="profile">
        <div className="profile__wrapper">
          <button className="profile__avatar" onClick={props.onEditAvatar}
            style={{ backgroundImage: `url(${userAvatar})` }}>
          </button>
          <div className="profile__form">
            <h1 className="profile__name">{userName}</h1>
            <button className="profile__button-edit" onClick={props.onEditProfile} type="button"></button>
            <p className="profile__interst">{userDescription}</p>
          </div>
        </div>
        <button className="profile__button-add" type="button" onClick={props.onAddPlace}></button>
      </section>
      
      <section className="cards-container">
      {cards.map((card, i) => (
        <Card card={card} key={i} onCardClick={props.onCardClick} />
        ))}
      </section>

    </main>
  )
}

export default Main